/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html {
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}

audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

[hidden],
template {
	display: none;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

abbr[title] {
	border-bottom: 1px dotted;
}

b,
strong {
	font-weight: bold;
}

dfn {
	font-style: italic;
}

h1 {
	font-size: 2em;
}

mark {
	background: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
}

svg:not(:root) {
	overflow: hidden;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
}

pre {
	overflow: auto;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input {
	line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	border: 0;
	padding: 0;
}

textarea {
	overflow: auto;
}

optgroup {
	font-weight: bold;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
	color: #404040;
	font-family: 'Neuton', serif !important;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	font-family: "Neuton", serif;
}

p {
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 0.9375rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #fff;
	/* Fallback for when there is no custom background color defined. */
}

blockquote,
q {
	quotes: "" "";
}

blockquote:before, blockquote:after,
q:before,
q:after {
	content: "";
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul, ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 1em 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

article {
	margin: 0 !important;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), inset 0 15px 17px rgba(255, 255, 255, 0.5), inset 0 -5px 12px rgba(0, 0, 0, 0.05);
	color: rgba(0, 0, 0, 0.8);
	font-size: 12px;
	font-size: 0.75rem;
	line-height: 1;
	padding: .6em 1em .4em;
	text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	border-color: #ccc #bbb #aaa;
	box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.8), inset 0 15px 17px rgba(255, 255, 255, 0.8), inset 0 -5px 12px rgba(0, 0, 0, 0.02);
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
	box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: royalblue;
}

a:visited {
	color: purple;
}

a:hover, a:focus, a:active {
	color: midnightblue;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation {
	width: 100%;
	z-index: 30;
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 auto;
	top: 0px;
}

.main-navigation:after {
	content: "";
	height: 200px;
	background: red;
}

.main-navigation .logo {
	padding: 40px;
}

.main-navigation .logo-container {
	background: url(/wordpress/wp-content/themes/humescores/assets/img/logo_bg.svg) center no-repeat;
	background-size: contain;
	height: 250px;
	width: 300px;
}

.main-navigation .menu-menu-container {
	position: absolute;
	left: 20%;
	margin-left: -50px;
	top: 45%;
	margin-top: -50px;
}

.main-navigation .menu-right-menu-container,
.main-navigation .menu-left-menu-container {
	margin-top: -60px;
}

.main-navigation .menu-left-menu-container {
	width: 20%;
}

.main-navigation .menu-right-menu-container {
	width: 30%;
}

.main-navigation #right-menu {
	padding-right: 40px;
}

.main-navigation #left-menu,
.main-navigation #right-menu {
	list-style-type: none;
	display: flex;
	margin: 0px;
	justify-content: space-around;
}

.main-navigation #left-menu li > a:link,
.main-navigation #right-menu li > a:link {
	text-decoration: none;
}

/* Small menu. */
.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
	margin: 0 0 1.5em;
	overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	float: left;
	width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	float: right;
	text-align: right;
	width: 50%;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.byline,
.updated:not(.published) {
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
	width: 100%;
	height: 800px;
	background: url(/wordpress/wp-content/themes/humescores/assets/img/floor-new.jpg) center no-repeat fixed;
	position: absolute;
	background-size: cover;
	display: flex;
	justify-content: center;
	align-content: center;
}

.site-footer .contact-container {
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
	min-height: 270px;
	height: 810px;
	margin: 0 auto;
	width: 95%;
	background: #fff;
	flex-wrap: wrap;
	display: flex;
	top: -90px;
	position: absolute;
}

.site-footer .contact-container-col1 {
	width: 60%;
	background: #bc201e;
}

.site-footer .contact-container-col1 .contact-mail {
	background: #fff;
	width: 100%;
	display: inline-block;
	height: 40%;
	padding: 80px;
}

.site-footer .contact-container-col1 .contact-mail h1 {
	position: absolute;
	top: 50px;
}

.site-footer .contact-container-col1 .contact-mail .mail {
	background: transparent;
	border-bottom: black;
	border: none;
	font-size: 20px;
	font-family: "Neuton", serif;
	color: #ad8b8b;
}

.site-footer .contact-container-col1 .contact-mail .mail-horizontal-line {
	background-color: black;
	width: 500px;
	float: left;
	margin-top: -10px;
	font-weight: 800;
	height: 1px;
}

.site-footer .contact-container-col1 .contact-mail .submit-btn {
	margin-left: 50px;
	background-color: #bc201e;
	box-shadow: none;
	border-radius: 0px;
	border: none;
	color: #fff;
	display: inline-block;
	padding: 11px 55px;
	margin-right: -40px;
	margin-top: 30px;
	font-family: 'PT Serif', serif;
	font-size: 14px;
	border: 2px solid #bc201e;
	font-weight: 400;
	letter-spacing: .28px;
	float: right;
}

.site-footer .contact-container-col1 .contact-form {
	background: #bc201e;
	position: absolute;
	display: flex;
	width: inherit;
	height: 550px;
}

.site-footer .contact-container-col1 .contact-form-address, .site-footer .contact-container-col1 .contact-form-picture {
	width: 50%;
}

.site-footer .contact-container-col1 .contact-form-address {
	background: #bc201e;
	display: flex;
	flex-direction: column;
	text-align: center;
	color: #fff;
	font-family: 'PT Serif', serif;
	font-size: 18px;
	font-weight: 400;
	line-height: 22px;
	padding: 50px;
}

.site-footer .contact-container-col1 .contact-form-address .pin-image {
	width: 50px;
	margin: 0 auto;
}

.site-footer .contact-container-col1 .contact-form-picture .address-image img {
	position: absolute;
	height: 100% !important;
}

.site-footer .contact-container-col1 .address-picture {
	height: 100%;
}

.site-footer .contact-container-col1 .address-picture img {
	height: inherit !important;
}

.site-footer .contact-container-col2 {
	width: 40%;
	background: url(/wordpress/wp-content/themes/humescores/assets/img/reka.png) no-repeat right 75px;
	background-size: contain;
}

.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.header-background {
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	width: 100%;
	overflow: hidden;
	height: 170px;
}

.header-background:after {
	content: "";
	background: url(/wordpress/wp-content/themes/humescores/assets/img/top_menu.svg) center no-repeat;
	z-index: -1;
	background-size: 100% 100%;
	position: absolute;
	left: -20px;
	right: -20px;
	bottom: 0;
	top: 0;
}

.container {
	display: block;
	width: 100%;
}

#wp-custom-header-video {
	width: 100%;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

@media (max-width: 1000px) {
	#right-menu,
	#left-menu {
		display: none;
	}
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
## Main Page RWD
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
@media (max-width: 1000px) {
	.menu-right-menu-container,
	.menu-left-menu-container {
		display: none;
	}
}

@media (max-width: 600px) {
	.facebook-text {
		padding: 0px 20px;
		font-size: 5vw;
	}
	.dash-section {
		height: 60%;
	}
	.dash-inner-container {
		display: flex;
		flex-direction: column;
		position: relative;
	}
	.dash-inner-container .elem:nth-child(4) {
		display: none;
	}
	.dash-inner-container .elem:nth-child(3) {
		transform: translateY(0%);
	}
	.dash-inner-container .elem:nth-child(1),
	.dash-inner-container .elem:nth-child(2),
	.dash-inner-container .elem:nth-child(3),
	.dash-inner-container .elem:nth-child(5),
	.dash-inner-container .elem:nth-child(6),
	.dash-inner-container .elem:nth-child(7) {
		position: relative !important;
		left: 0 !important;
		top: 0;
		right: 0% !important;
		display: inline-block;
		margin: 40px 0px;
	}
	.our-superity-section-outer .info-container .info-container-title h1 {
		font-size: 34px;
		line-height: 37.67px;
		letter-spacing: .66px;
		padding-top: 30px;
	}
	.wood-bg .inner-container .event-btn {
		width: 90vw;
	}
}

@media (max-width: 1300px) {
	.facebook-text {
		padding: 0px 20px;
	}
	.offer-2 {
		margin-top: 350px;
	}
	.our-superity-section {
		height: 70%;
	}
	.our-superity-section-outer {
		display: block;
		height: inherit;
	}
	.our-superity-section-outer .bear-container {
		display: none;
	}
	.our-superity-section-outer .info-container {
		width: 100%;
		padding: 20px 0px;
	}
	.our-superity-section-outer .info-container .info-container-text {
		padding: 10px 30px;
	}
	.our-superity-section-outer .calendar-container {
		padding: 0px;
		width: 100%;
	}
	.our-superity-section-outer .calendar-container .calendar::before {
		right: 0%;
		background-size: cover;
		margin: -27px auto 0px auto;
		position: relative;
		width: 100%;
	}
	.dish-offer {
		display: unset;
	}
	.dish-offer .dish-offer-container {
		display: unset;
	}
	.dish-offer .dish-offer-container .image-container {
		width: 100%;
